import axios from 'axios'
import { getToken, removeToken, setToken } from '@/utils/token.js'
import { delStorage } from '@/utils/storage'

import { getNewTokenAPI } from '@/api/index.js'
import router from '@/router/index.js'
import Notify from '@/ui/Notify.js'
const http = axios.create({
    baseURL: 'http://toutiao.itheima.net',
    // baseURL: 'http://geek.itheima.net',
    timeout: 20000,
})


// 
http.interceptors.request.use(function (config) {
    //  ?.  ?lengthundefined
    // token0 config.headersAuthorization
    // console.log(getToken().length)
    if (getToken()?.length > 0 && config.headers.Authorization === undefined) {
        config.headers.Authorization = `Bearer ${getToken()}`
    }
    return config
}, function (error) {
    // 
    // console.log('cuole')
    return Promise.reject(error)
})

// 
http.interceptors.response.use(function (response) {
    // http2xx,3xx
    // 
    return response
}, async function (error) {
    // http4xx,5xx
    // 
    // console.dir(error)
    if (error.response.status === 401) {
        // Notify('')
        removeToken()

        // // 1:Token,,
        // // routerthisundefined this.$router.push('/login')
        // // router.currentRoutethis.$route
        // // fullPath #
        // // 401
        // router.replace(`/login?path=${router.currentRoute.fullPath}`)

        // 2 refresh_tokentiken
        const { data: res } = await getNewTokenAPI()
        // token
        // 1.token
        setToken(res.data.token)
        // 2.token
        error.config.headers.Authorization = `Bearer ${res.data.token}`
        // 3.
        // 4xx5xx,error
        // error.configbaseURL
        // ,token
        // ,return,return Promise.reject(error)
        return http(error.config)

    } else if (error.response.status === 500 && error.config.url === '/v1_0/authorizations' && error.config.method === 'put') {
        // refresh_token
        Notify({ type: 'warning', message: ',' })
        // token
        removeToken()
        // loc
        delStorage('mylogin_key')
        delStorage('refresh_key')
        router.replace(`/login?path=${router.currentRoute.fullPath}`)
        // router.replace('/login')


    }

    return Promise.reject(error)
})
// :401
// 1.token,,,token
// ,
// 2.token,refresh_token,,token
// ,,
// 1.loc.setitem(refresh_token)refresh_token
// 2.401tokenapi,token
// 3.errorheaderstokenPromise
// 4.

//  axios,param,header
export default ({ url, method = 'GET', params = {}, data = {}, headers = {} }) => {
    return http({
        url,
        method,
        params,
        data,
        headers
    })
}