// 
import axios from '@/utils/request.js'
//  header
import { getStorage } from '@/utils/storage.js'
// -Token
export const getNewTokenAPI = () => {
    return axios({
        url: '/v1_0/authorizations',
        method: 'PUT',
        // Authorization,tokentoken
        headers: {
            Authorization: `Bearer ${getStorage('refresh_token')}`
        }
    })
}